	export default {
		data() {
			return {
				/**
				* checked:是否选中
				* inputname:input账号名称value值
				* name:接口调用账号名称
				* inputpas：input密码value值
				* pass:密码数据
				*/
				checked:false,
				inputname:'',
				name:'',
				inputpas:'' ,
				pass:'zcmcss'
			}
		},
		onLoad() {
			
		},
		methods: {
			/**
			* @disClick:是否选中
			*/
			disClick(){
				this.checked=!this.checked
			},
			/**
			* formSubmit:登录提交信息
			*/
			formSubmit: function(e) {
				let _this = this
				_this.inputname = e.detail.value.input
				_this.inputpas = e.detail.value.pas
				uni.request({
					url:'http://ceshi3.dishait.cn/api/login',
					data:{
						username : 'user2',
						password : 'zcmcss'
					},
					method:'POST',
					success:(res) =>{
						this.name = res.data.data.username
						console.log(res)
						if(_this.inputname==""||_this.inputpas==""){
							uni.showModal({
									content: '输入不能为空',
									showCancel: false
							    });
						}else if(_this.inputname===_this.name){
							if(_this.inputpas===_this.pass){
								if(_this.checked==true){
									uni.setStorage({
										key:"name",
										data:_this.name
									})
									uni.showModal({
										content: '登录成功',
										showCancel: false
									}).then(()=>{
										uni.switchTab({
											url:"../index/index"
										})
									})
									
								}else{
									uni.showModal({
										content: '您未同意协议',
										showCancel: false
									});
								}
							}else{
								uni.showModal({
								    content: '密码错误',
								    showCancel: false
								});
							}
						}else{
							uni.showModal({
							    content: '不存在此用户',
							    showCancel: false
							});
						}
					}
				})
				
            },
		}
	}
